@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&family=Poppins:wght@300&display=swap');
.navbar-items{
    background-color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0px 30px;
    box-shadow: 0px 5px 15px 0px rgb(0,0,0,0.25);
    width: 95%;
    height: 88px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 9999;
}
span{
    color: gold;
}
.navbar-logo{
    color:black;
    font-size: 2rem;
    cursor :pointer;
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
    /* font-family: 'Poppins', sans-serif; */
}
.nav-menu{
    display:grid;
    grid-template-columns: repeat(6,auto);
    grid-gap: 10px;
    align-items: center;
    list-style: none;
}
.nav-links{
 text-decoration: none;
 color: black;
 font-size: 1.2rem;
 font-weight: 600;
 padding :0.7rem 1rem;
 white-space: nowrap;
}

.nav-links i{
    padding-right: 10px;

}
.nav-links:hover{
    background-color: gold;
    color: black;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}
.nav-button{
    display: none;
}
button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border:none;
    cursor: pointer;

}
.menu-icon{
    display: none;
}

button:hover{
    background-color: black;
    color:whitesmoke;
}


@media screen and (max-width:850px){

    .navbar-items{
        z-index:99;
        
    }
   
    .nav-menu{
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
        background-color:white;
        width: 100%;
        height: auto;
        position: absolute;
        top:0;
        left:-100%;
        opacity: 1;
        align-items: center;
        padding: 80px 0px 30px 0px;
        margin:0;
        transition: 0.3s ease-in-out;
        border-radius: 13px;
    }
    .nav-menu.active{
        left:0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out;
        
    }
    .nav-menu.active li{
        
        width: 100%;
    }
    .nav-links{ 
        display: block;
        width:100%;
        font-size: 1.2rem;
        padding:2rem 0;
       }
    .nav-links:hover{
        background-color: gold;
        transition: none;
        /* margin-left: 100px;
        margin-right:100px ; */
        
    }
    button{
        display: none;
     }
     .nav-button{
        display: block;
        text-align: center;
        margin: auto;
        padding: 1.5rem;
        border-radius: 4px;
        width: 80%;
        background-color: black;
        text-decoration: none;
        color: white;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
    }
    .menu-icon{
        display: block;
        cursor: pointer;
    }
    .menu-icon i{
        font-size: 1.2rem;
        color: black;
    }
}