.footer{
    padding: 4rem 6rem;
    background-color: black;
    color: white;
}
.top-foot{
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: start;
    flex-wrap: wrap;

}

.top-foot i{
    color: white;
    font-size: 2rem;
    margin-left: 1rem;
}
.top-foot i:hover{
    color: gold;
}

.bottom-foot{
    padding-top: 2rem;
    text-align: start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.bottom-foot div{
    display: flex;
    flex-direction: column;
}

.bottom-foot h1{
    font-family: 1.3rem;
    padding:1rem 0 0.8rem 0;
}

.bottom-foot li{
    display: flex;
    flex-direction: column;
    color: aliceblue;
    text-decoration-style: none;
    font-size: 1.1rem;
}

@media screen and (max-width:850px){

    .footer{
        padding : 4rem 2rem;
    }
    .footer i{
        margin:1rem 1rem 0 0;
    }
    .bottom-foot div{
        width: 50%;
    }
}

