.login-img{
    width: 100%;
    height: 100vh;
    position: relative;
}
.login-form{
    position: absolute;
    background-color:white;
    margin:10rem 25rem; 
    color: #2a2a2a;
    padding: 15px 5px;
    box-shadow: 0px 5px 10px 0px gold;
    width: 32%;
    height: 50%;
    border-radius: 13px;
    transform: translate(-50%,-70%);
    top: 40%;
    left: 25%;

}
.login-form h1{
    text-align: center;
    color:black;
    margin-top: 20px;
}

.login-form span{
    color: gold;
}

.form-input{
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    width: 70%;
    margin:auto;
}
.username{
    border: 5px solid black;
    font-size: large;
    background-color:white;
    font-weight: 500;
    
}

.login-form input{
    height: 3rem;
    padding: 0.1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border: 1px solid #2a2a2a;
}
.login-form ::placeholder{
    font-size: 1.1rem;
    color:black;
    font-weight: 600;
}

.login-form textarea {

    padding: 0.1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border: 1px solid #2a2a2a;
}
.login{
    text-decoration: none;
    
}

.login-form button{
    display: inline;
    /* border: 2px solid black; */
    margin-left: 5rem;
    width: 50%;
    font-size: 600;
    letter-spacing: 1px;
    background-color:black;
    color:white;
    }
.login-form button:hover{
    background-color: gold;
    color: black;
    border: 2px solid black;
}
@media screen and  (max-width:850px) {

    .login-form{
        background-color: white;
        margin:15rem 4rem;
        color: #2a2a2a;
        padding: 15px 5px;
        box-shadow: 0px 5px 10px 0px gold;
        width: 80%;
        height: 50%;
        border-radius: 13px;
        align-items: center;
        left: 40%;
        top:30%
    }
    
   .login-input{
      padding-top: 2rem;
      width: 90%;
   }
   .login-form button{
    margin-left: 2.5rem;
    width: 60%;
   }
}