.trip{
    margin: 4rem 6rem;
    color: black;
    text-align: center;
}
.trip h1{
    font-size: 3rem;
}
.trip q{
    font-size: 1.5rem;
}
.trip-card{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.trip-data{
    width: 32%;
    box-shadow:0 5px 25px rgba(0,0,0,0.19);
    border-radius: 7px;
    padding: 1rem 0.8rem;
    cursor: pointer;
}
.t-image{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;


}
.t-image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.t-image:hover img {
    transform: scale(1.1);
}
.trip-card h4{
    font-size: 1.3rem;
    padding: 0.9rem 0.2rem 0;
}

@media screen and (max-width : 850px){
    .trip{
        margin: 4rem 2rem;
    }
    .trip-card{
       flex-direction: column;
    }
    .trip-data{
        width: 100%;
        margin-bottom: 1.5rem;
        
    }

}