.home-front{
    width: 100%;
    height:100vh;
    position: relative;
}
.about{
    width: 100%;
    height:70vh;
    position: relative;
}
.service{
    
        width: 100%;
        height:70vh;
        position: relative;
}
.about h1{
    /* margin-top: 50px !important; */
    top : 30% !important;
    padding : 8rem !important;  
    }  


.contact{
    width: 100%;
    height:70vh;
    position: relative;
}

img{
    width:100%;
    height:100%;
    object-fit:cover;
}

.home-text{
    position: absolute;
    top:38%;
    left: 50%; 
    transform: translate(-50%,-50%);
    width:100%;
    text-align: center;
}

.home-text h1{
    font-size: 3rem;
    font-weight: 800;
    background:black;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent; 
}

.home-text p{
   font-size: 1.8rem;
   color:gold;
   padding: 0.5rem 0 2rem 0;
   font-weight: 600;

}
.home-text .home-button{
    text-decoration: none;
    background-color: white;
    border:solid black  3px;
    margin-top: 10px;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    font-size: 1.1rem;
    font-weight: 700;
    letter-spacing: 0.5px;
    color: black;
}
.home-text .home-button:hover{
    background-color:black;
    
    color:white;
}
@media screen and (max-width:555px){
    .home-text h1{
        padding:10px 20px;

    } 
    .hom-text p{
        font-size:1.1rem;
        padding:0 0 2rem 0;
        font-weight: 900;
    }
    .home-text .home-button{
        
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;    
}
}