.signup-img{
    width: 100%;
    height: 100vh;
    position: relative;
}
.signup-form{
    position: absolute;
    background-color: white;
    margin:10rem 25rem; 
    color: #2a2a2a;
    padding: 15px 5px;
    box-shadow: 0px 5px 10px 0px gold;
    width: 35%;
    height: 70%;
    border-radius: 13px;
    transform: translate(-50%,-70%);
    top: 50%;
    left: 15%;

}
.signup-form h1{
    text-align: center;
    color:black;
}

.signup-form span{
    color: gold;
}

.form-input{
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    width: 70%;
    margin:auto;
    
}

.signup-form input{
    height: 3rem;
    padding: 0.1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border: 1px solid #2a2a2a;
}
.signup-form ::placeholder{
    font-size: 1.1rem;
}

/* .signup-form textarea {

    padding: 0.1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border: 1px solid #2a2a2a;
} */
.submit{
    text-decoration: none;
    

}
.signup-form button{
    margin-left: 6rem;
    display: inline;
    width: 50%;
    /* border: 2px solid black; */
     font-size: 600;
    letter-spacing: 1px;
    background-color: black;
    color: white;
    }
 .signup-form button:hover{
     background-color: gold;
     color: black;
     border: 2px solid black;
} 
/* img{
    width: 100%;
    height: 100%;
    object-fit: cover;
} */
@media screen and  (max-width:850px) {

    .signup-form{
        background-color: white;
        margin:15rem 4rem;
        color: #2a2a2a;
        padding: 15px 5px;
        box-shadow: 0px 5px 10px 0px gold;
        width: 70%;
        height: 70%;
        border-radius: 13px;
        align-items: center;
        left: 40%;
        top:40%
    }
    
   .form-input{
      padding-top: 2rem;
      width: 90%;
   }
   .signup-form button{
    margin-left: 3rem;
   }
}